import React from "react";
import Container from "../../common/Container";
import Header from "../../common/Header";
import Section from "../../common/Section";

export default () => (
  <Container>
    <Header title="O autorze" />

    <Section
      title="Asia Grniak"
      body={
        <>
          Cze, mam na imi Asia! Na co dzie mieszkam w Albanii, gdzie
          prowadz wasny biznes, a w midzyczasie rozwijam swoje umiejtnoci
          programistyczne. Moim celem jest wejcie w wiat IT i rozwj w tym
          kierunku. Jednak nie sam prac yje czowiek! Moje serce skrady
          podre, odkrywanie nowych miejsc, kultur i ludzi to moja prawdziwa
          pasja. A w chwilach relaksu uwielbiam spdza czas z moimi ukochanymi
          kotami, ktre zawsze umilaj mi dzie.
        </>
      }
    />
  </Container>
);
